import { _decorator, BoxCollider2D, CircleCollider2D, Collider, Collider2D, Component, Contact2DType, EventTouch, Input, Vec3 } from 'cc';
import { SingleContact } from './SingleContact';
const { ccclass, property } = _decorator;

@ccclass('Draggable')
export class Draggable extends Component {


    private bindedData:number;
    
    start() {

     

    }

    update(deltaTime: number) {
        
    }
    onLoad() {
       
    this.node.on(Input.EventType.TOUCH_START, this.onTouchStart, this);
    this.node.on(Input.EventType.TOUCH_MOVE, this.onTouchMove, this);
    this.node.on(Input.EventType.TOUCH_END, this.onTouchEnd, this);
    let collider= this.getComponent(CircleCollider2D);
    
    collider.on(Contact2DType.BEGIN_CONTACT, this.onBeginContact, this);
   
  
    }



    onBeginContact() {
        console.log("CONTACTOOOOOOOO")
    }
    
    onTouchStart(event: EventTouch) {};
    onTouchMove(event: EventTouch) {
       
        let pos = event.getUILocation();
        this.node.setWorldPosition(new Vec3(pos.x, pos.y, 0));
    }
    onTouchEnd(event: EventTouch) {}


}


